<template>
    <div class="view-wrapper" :style="{minHeight:pageMixin_browser.clientHeight}">
        <!--        <DP_Popup v-if="!pageParameter.activity"-->
        <!--                  :display.sync="tips.show"-->
        <!--                  :show-close="true"-->
        <!--                  background-color="white"-->
        <!--                  :title="tips.title"-->
        <!--        >-->
        <!--            {{tips.content}}-->
        <!--        </DP_Popup>-->
        <template v-if="type">
            <div class="vip" v-if="type=== 'general'">
            </div>
            <div class="top">
                <div class="logo">
                    <img src="../../../assets/ca/ca_logo.png" alt="">
                </div>
                <div class="slogan"></div>
                <p class="text1 text">
                    {{source[type].text1}}
                </p>
                <p class="text2 text">
                    {{source[type].text2}}
                </p>
                <div v-if="type=== 'general'" class="award_vip">
                    <b>{{pageParameter.vipUpperLimit}}</b><img src="../../../assets/ca/recommend/vip.png" alt="">
                </div>
                <div v-else class="award_cash">
                    <p> 最高<b>{{pageParameter.rewardUpperLimit}}</b>元</p>
                    <p>奖学金</p>
                </div>
                <!--            <div class="award" :style="{backgroundImage:'url('+source[type]['award']+')'} ">-->
                <!--                30-->
                <!--            </div>-->

                <button class="btn" v-if="!this.$store.getters.getBrowser.device.isDPApp"
                        @click="checkReward" type="button">查看我的奖励
                </button>
            </div>
        </template>

        <!--        <div class="board">-->

        <!--        </div>-->

        <div class="board_2">
            <div class="title">简单四步，解锁好礼</div>
            <div class="content">
                <img class="icon" src="../../../assets/ca/recommend/board/1.png" alt="">
                <img class="icon" src="../../../assets/ca/recommend/board/2.png" alt="">
                <img class="icon" src="../../../assets/ca/recommend/board/3.png" alt="">
                <img class="icon" src="../../../assets/ca/recommend/board/4.png" alt="">
                <img class="line" src="../../../assets/ca/recommend/board/l.png" alt="">
                <div class="font">
                    <span>点击生成邀请海报</span>
                    <span>分享专属活动海报</span>
                    <span>好友购买活动课程</span>
                    <span v-if="type==='vip'">获得现金红包</span>
                    <span v-else>获得VIP会员时长</span>
                </div>
            </div>
        </div>

        <div class="rule">
            <div class="title">详细规则</div>
            <div class="content">
                <p style=" white-space: pre-line;">
                    {{pageParameter.activityRule}}
                </p>
                <!--                <p>1、将购买成功的课程分享给好友，好友通过你分享的链接或海报，开团或单购</p>-->

                <!--                <p>2、好友购买成功，你将获得奖学金，暂存在你的奖学金账户中，可累计，不设上限，可随时提现</p>-->

                <!--                <p>3、奖学金每天只可以提现1次，提现后于24小时内到账，提出现金不能少于2元。如好友退款，将扣除奖学金。如有问题，请咨询客服</p>-->

                <!--                <p>4、奖学金每天只可以提现1次，提现后于24小时内到账，提出现金不能少于2元。如好友退款，将扣除奖学金。如有问题，请咨询客服</p>-->
            </div>
        </div>
        <div v-show="this.$route.query.user_id || this.userInfo.id" class="flotage">
            <DP_Button  type="primary" @click.native="poster">生成邀请海报</DP_Button>
        </div>
    </div>
</template>

<script>
    import pageMixin from "@/mixin/pageMixin";
    import {mapGetters} from "vuex";
    import {checkUserType} from "@/api/parameter";
    // import DP_Popup from '../../../components/DP_Popup'
    import DP_Button from '../../../components/DP_Button'
    import {sernsorFn} from "@/utils/sensors";

    export default {
        metaInfo() {
            return {
                title: this.source[this.type].title || '熊猫博士', // set a title
                meta: [{                 // set meta
                    name: 'keywords',
                    content: this.$route.meta.title + ",熊猫博士"
                }],
            }
        },
        name: "index",
        components: {
            // DP_Popup,
            // eslint-disable-next-line vue/no-unused-components
            DP_Button
        },
        computed: {
            ...mapGetters({
                pageParameter: "getPageParameter",
                userInfo: "getUserInfo",
            }),
        },
        props: {},
        mixins: [pageMixin],
        data() {
            return {
                type: '',// general vip
                tips: {
                    show: true,
                    title: "提示",
                    content: "该活动已经下线"
                },
                source: {
                    general: {
                        title: "邀好友，得VIP时长",
                        text1: "免费会员 一荐获取",
                        text2: "每邀请一个好友成功购买熊猫博士VIP即可获得",
                        award: require("../../../assets/ca/recommend/vip.png"),
                    },
                    vip: {
                        title: "邀好友，得现金红包",
                        text1: "现金奖学金，多荐多得，上不封顶",
                        text2: "每邀请一个好友成功购买识字课程即可获得",
                        award: require("../../../assets/ca/recommend/60.png"),
                    },

                }


            }
        },
        watch: {
            pageParameter(val) {
                if (!val.isInit && val.activity === false) {
                    this.dp_Tips('提示', '分享活动已结束，下次再来哟');
                }

            }
        },
        methods: {

            showPopup(title, content) {
                this.tips.title = title;
                this.tips.content = content;
                this.tips.show = true;
            },
            closePopup() {
                this.tips.show = false;
            },
            pageMixin_wechatJsSdkFn(wx) {
                wx.updateAppMessageShareData({
                    // title: this.$route.meta.title || '熊猫博士', // 分享标题
                    // desc: this.$route.meta.desc || '', // 分享描述
                    title:'熊猫博士识字-分享得好礼', // 分享标题
                    desc: '', // 分享描述
                    link: window.location.href, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://business.xiongmaoboshi.com/cms_media/200.png', // 分享图标
                    success: function () {
                        // 设置成功
                    }
                })

                wx.updateTimelineShareData({
                    title: this.$route.meta.title || '熊猫博士', // 分享标题
                    link: window.location.href, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://business.xiongmaoboshi.com/cms_media/200.png', // 分享图标
                    success: function () {
                        // 设置成功
                    }
                });
            },
            checkUserInApp() {
                const {user_type, user_id,} = this.$route.query;
                if (user_type && user_id) {
                    switch (user_type) {
                        case 'vip':
                            this.type = user_type;
                            break;
                        case 'general':
                            this.type = user_type;
                            break;
                        default:
                            this.type = "general";
                    }
                    if (this.$store.getters.getBrowser.device.isDPApp) {
                        // this.$store.commit('setUserInfo',{userType:user_type,id:user_id});
                    }
                }
            },
            checkReward() {
                sernsorFn('buttonClick', {
                    tabTitle: '活动推广页',
                    moduleTitle: 'Referral',
                    buttonType: "普通按钮",
                    buttonName: "查看我的奖励"
                });
                this.dp_go('invitation-detail');

            },
            async checkUserInWechat() {
                const res = await checkUserType("com.drpanda.chineseacademy");
                const {data: {data, code}} = res;
                if (code === 0) {
                    this.type = data ? 'vip' : 'general';
                    document.title = this.source[this.type].title;
                }
            },
            async poster() {
                sernsorFn('buttonClick', {
                    tabTitle: '活动推广页',
                    moduleTitle: 'Referral',
                    buttonType: "普通按钮",
                    buttonName: "生成海报"
                })
                const parameter = {
                    user_id: this.$route.query.user_id ? this.$route.query.user_id : this.userInfo.id,
                    avatar: this.userInfo.avatar,
                    name:this.userInfo.name,
                    sentence: this.pageParameter.posterAndShareContent.length > 0 ? this.pageParameter.posterAndShareContent[0].shareContent : '',
                    poster: this.pageParameter.posterAndShareContent.length > 0 ? this.pageParameter.posterAndShareContent[0].url : '',
                    bundleId: "com.drpanda.chineseacademy",
                };

                this.dp_go('poster2', {parameter: encodeURIComponent(JSON.stringify(parameter))});
            }
        },
        created() {
            this.$store.getters.getBrowser.device.isWechat ? this.checkUserInWechat() : this.checkUserInApp();
        },
        mounted() {

        },
    }
</script>

<style lang='scss' scoped>
    @import "../../../assets/global/style/mixin";

    .view-wrapper {
        background-color: #ff4438;
        /*background-image: url("../../../assets/ca/recommend/b.png");*/
        overflow: hidden;
        position: relative;
        padding-bottom: 350px;
        @include animation_bg("../../../assets/global/bg.png");

        .top {
            color: #ffffff;
            overflow: hidden;
            text-align: center;
            position: relative;
            z-index: 1;
            opacity: 0;
            animation: fadeIn 1.5s ease .5s 1;
            animation-fill-mode: forwards;

            .logo {
                width: 215px;
                margin: 25px auto;

                img {
                    width: 100%;
                }
            }

            .slogan {
                width: 764px;
                height: 210px;
                margin: 0px auto;
                background-size: 100% 100%;
                background-image: url("../../../assets/ca/recommend/jdsg.png");
            }

            .text {
                margin: 20px auto;

                b {
                    font-size: 240px;
                }
            }

            .text1 {
                font-size: 55px;
                text-shadow: 0 5px 20px rgba(139, 87, 42, 0.53);
            }

            .text2 {
                font-size: 40px;
                color: #650b16;
                width: 100%;
                font-weight: bold;

            }

            .award_vip {

                width: 800px;
                height: 311px;
                /*animation: pulse 8s linear infinite;*/
                background-repeat: no-repeat;
                margin: 0 auto;
                margin-top: 150px;
                margin-bottom: 30px;
                font-size: 260px;
                text-align: center;
                font-weight: bold;
                border-radius: 50px;
                border: solid 6px #ffe881;

                b {
                    display: inline-block;
                    margin: 0 60px;
                    font-size: 200px;
                    animation: pulse 3.5s linear infinite;
                }

                img {
                    width: 240px;
                    height: 208px;

                }
            }

            .award_cash {
                width: 800px;
                padding: 20px 20px;
                margin: 0 auto;
                margin-top: 50px;
                margin-bottom: 30px;
                font-size: 60px;
                border-radius: 50px;
                border: solid 6px #ffe881;

                b {
                    display: inline-block;
                    margin: 0 60px;
                    font-size: 200px;
                    animation: pulse 3.5s linear infinite;
                }
            }

            .btn {
                background-color: transparent;
                /*border-radius: 20px;*/
                border: none;
                text-decoration: underline;
                padding: 30px;
                color: white;
                width: 440px;
                font-size: 44px;
            }
        }

        .vip {
            z-index: 1;
            width: 1080px;
            height: 750px;
            background-image: url("../../../assets/ca/recommend/vip_bg.png");
            background-size: 100% 100%;
            position: absolute;
            top: 0;
            right: 0;
            opacity: 0;
            animation: fadeInDownBig 1.5s ease 1;
            animation-fill-mode: forwards;
            /*background-size: cover;*/

        }

        .board {
            position: relative;
            z-index: 1;
            background-image: url("../../../assets/ca/recommend/board.png");
            width: 924px;
            height: 462px;
            background-size: 100% 100%;
            margin: 30px auto;

        }

        .board_2 {
            width: 924px;
            height: 462px;
            margin: 50px auto;
            background-color: #fff7e1;
            border-radius: 30px;
            display: flow-root;
            position: relative;
            z-index: 1;

            .title {
                background-color: #ffd70f;
                color: #835d14;
                width: 540px;
                height: 92px;
                line-height: 92px;
                text-align: center;
                border-radius: 30px;
                margin: 0 auto;
                font-size: 39px;
                font-weight: bold;
                margin-top: -30px;

            }

            .content {
                padding: 0 25px;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-around;

                .icon {
                    margin-top: 35px;
                    width: 120px;
                    height: 120px;
                }

                .line {
                    width: 80%;
                    margin: 20px 0;
                    height: 27px;
                }

                .font {
                    display: flex;
                    width: 100%;
                    justify-content: space-around;

                    span {
                        text-align: center;
                        display: inline-block;
                        width: 200px;
                        font-size: 30px;
                    }
                }
            }
        }

        .rule {
            position: relative;
            z-index: 2;
            border-radius: 30px;
            box-sizing: border-box;
            margin: 90px auto;
            margin-top: 550px;
            width: 924px;
            background-color: white;
            color: #6e6e6e;
            font-size: 39px;
            padding: 130px 70px 70px 70px;
            position: relative;

            &:before {
                content: '';
                display: block;
                width: 784px;
                height: 490px;
                position: absolute;
                top: -470px;
                z-index: -1;
                bottom: 0;
                left: 50%;
                margin-left: -392px;
                background-image: url('../../../assets/ca/recommend/zi2.png');
                /*animation: bg 60s linear infinite alternate;*/
                transform: translateZ(0);
                background-size: 100% auto;
                background-position: center center;
                background-size: cover;
            }

            .title {
                background-color: #ffd70f;
                color: #835d14;
                width: 540px;
                height: 92px;
                line-height: 92px;
                text-align: center;
                border-radius: 30px;
                font-weight: bold;
                margin: 0 auto;
                margin-top: -200px;


            }

            .content {

                p {
                    margin: 30px 0;
                    line-height: 65px;
                }
            }


        }

        .flotage {
            width: 1080px;
            height: 240px;
            background-color: #ffffff;
            position: fixed;
            bottom: 0;
            left: 0;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            z-index: 10;
            opacity: 0;
            animation: fadeInUp .8s ease 1.5s;
            animation-fill-mode: forwards;

            button {
                background-color: #fd601a;
                color: white;
                width: 570px;
                height: 150px;
                line-height: 150px;
                border: none;
                border-radius: 75px;
                font-size: 54px;
            }
